import React from 'react';
import './UpcomingEvent.css';
import { useResolved } from '../../ppm/util/ResolvedContentContext';

const UpcomingEvent: React.FC = () => {
    const displayData = useResolved();
    const content = displayData.pages.home.upcomingEventContent;

    if (!content || !content.enabled) {
        return null;
    }

    return (
        <section className="ue-section">
            <div className="ue-container">
                <div className="ue-card">
                    <div className="ue-info">
                        <span className="ue-label">{content.label}</span>
                        <h2 className="ue-title">{content.title}</h2>
                    </div>

                    <div className="ue-schedule">
                        <div className="ue-schedule-item">
                            <div className="ue-location">{content.californiaLabel}</div>
                            <div className="ue-type">{content.californiaSubLabel}</div>
                            <div className="ue-date">{content.californiaDate}</div>
                        </div>

                        <div className="ue-schedule-item">
                            <div className="ue-location">{content.vietnamLabel}</div>
                            <div className="ue-type">{content.vietnamSubLabel}</div>
                            <div className="ue-date">{content.vietnamDate}</div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default UpcomingEvent;
