import React, { useEffect, useMemo, useState } from "react";
import "./PrayerTimes.css";


const PrayerTimes: React.FC = () => {
  const [times, setTimes] = useState({
    ca: new Date(),
    vn: new Date()
  });

  useEffect(() => {
    // Update time every second
    const timer = setInterval(() => {
      const now = new Date();
      setTimes({
        ca: new Date(now.toLocaleString("en-US", { timeZone: "America/Los_Angeles" })),
        vn: new Date(now.toLocaleString("en-US", { timeZone: "Asia/Ho_Chi_Minh" }))
      });
    }, 1000);

    return () => clearInterval(timer);
  }, []);

  const formatTime = (date: Date) => {
    return date.toLocaleTimeString("en-US", {
      hour: '2-digit',
      minute: '2-digit',
      hour12: true
    });
  };

  return (
    <div className="prayer-times-bar">
      <div className="prayer-content">
        <div className="timezone-container">
          <div className="timezone-item">
            <span className="tz-label desktop-only">California:</span>
            <span className="tz-label mobile-only">CA:</span>
            <span className="tz-time">{formatTime(times.ca)}</span>
          </div>
          <span className="tz-divider">|</span>
          <div className="timezone-item">
            <span className="tz-label desktop-only">Việt Nam:</span>
            <span className="tz-label mobile-only">VN:</span>
            <span className="tz-time">{formatTime(times.vn)}</span>
          </div>
        </div>

        <a href="https://sakyauniversity.com/" target="_blank" rel="noopener noreferrer" className="sbu-english-link">
          <span className="desktop-only">SBU English website</span>
          <span className="mobile-only">SBU English</span>
          <svg width="10" height="10" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M3.5 8.5L8.5 3.5" stroke="currentColor" strokeLinecap="round" strokeLinejoin="round" />
            <path d="M3.5 3.5H8.5V8.5" stroke="currentColor" strokeLinecap="round" strokeLinejoin="round" />
          </svg>
        </a>
      </div>
    </div>
  );
};

export default PrayerTimes;
